'use strict';

var React = require('react');
var appTypes = require('../../types/appTypes');
var Dom = require('react-dom');
var AutoSizeHelper = require('../common/AutoSizeHelper');
var styleHelper = require('../../helper/styleHelper');
var util = require('../../helper/util');

/**
 * 菜单栏组件
 */
var MenuBarItem = React.createClass({
  propTypes: {
    onClickEvent: React.PropTypes.func.isRequired,
    selected: React.PropTypes.bool.isRequired
  },

  render: function () {
    var backgroundStyle = {
      backgroundColor: this.props.selected ? this.props.selectedColor : undefined
    };

    return (<div style={backgroundStyle} className="item-bg" onClick={this.props.onClickEvent}>
      <label>{this.props.text}</label>
    </div>);
  }
});

var MenuBar = React.createClass({

  componentDidMount: function () {
    var selfDom = Dom.findDOMNode(this);
    var i = 0;
    var dom;
    var text;
    var size;
    var itemStyle;
    var left = 0;

    for (i = 0; i < selfDom.children.length; i++) {
      dom = selfDom.children[i];
      text = this.textArray[i];

      size = AutoSizeHelper.getComAutoSize(this.props, text);

      itemStyle = {
        width: (size.width + 30) + 'px',
        left: left + 'px'
      };

      left = left + size.width + 30;

      $(dom).css(itemStyle);
    }
    $(ReactDOM.findDOMNode(this)).bind('itemClick', this.onItemClick);
  },

  getInitialState: function () {
    return {
      selectedIndex: parseInt(this.props.selectedIndex, 10)
    };
  },

  render: function () {
    this.textArray = util.Text.delimitToWords(styleHelper.getText(this.props));
    var borderStyle = {
      borderColor: '#000',
      zIndex : 0,
      textAlign:'center',
      lineHeight:styleHelper.getHeight(this.props) + 'px'
    };
    styleHelper.StyleUtil(this.props,borderStyle).base().background().border().text();
    return (<div style={borderStyle}>
      {
        this.textArray.map(function (text, index) {
          text = text.trim();

          return (<MenuBarItem {...this.props}
                               key={index}
                               text={text}
                               selected={this.state.selectedIndex == index}
                               onClickEvent={this.onItemClick.bind(this, null, index)}
          />);

        }, this)
      }
    </div>);
  },

  onItemClick: function (e, index) {
    this.setState({selectedIndex: index});
  }

});

module.exports = MenuBar;




