'use strict';
var _ = require('underscore');
var request = require('superagent');
var assetsHelper = require('./assetsHelper');

var config = _.extend({
  iOSGuide: true,
  logo: assetsHelper.getAsset('logo.png'),
  showControlPanel: true, // 显示控制面板
  shell: '', // 外壳
  background: '#F0F0F0', // 背景色
  alwaysShowLinks: false, // 始终显示链接
  hoverShowLinks: false, // 鼠标滑过时显示链接
  hoverShowRemarks: false,
  showComments: true, // 显示批注
  showQRCode: true // 显示二维码
}, window.config);

exports.getConfig = function (cb) {
  if (window.mockplus) {
    return request.get('config.json?rnd=' + Math.random()).end(function (err, res) {
      if (err) {
        return cb(null, config);
      }
      try {
        var newConfig = _.extend(config, res.body);
        if (!newConfig.logo) {
          newConfig.logo = assetsHelper.getAsset('logo.png');
        }
        cb(null, newConfig);
      } catch (e) {
        cb(null, config);
      }
    });
  }
  cb(null, config);
};

exports.notForceUseCurrentBrowser = function () {
  return window.location.href.indexOf('force=1') === -1;
};



