'use strict';

var React = require('react');
var ReactDOM = require('react-dom');
var VerticalScrollBar = require('../common/VerticalScrollBar');
var TextParts = require('../common/TextParts');
var Border = require('../common/Border');
var appTypes = require('../../types/appTypes');

var TextArea = React.createClass({

  render: function () {
    return (<Border {...this.props}
        partStyle={{
          borderWidth: '1px',
          borderColor: '#000',
          zIndex:0
        }}
      >
      {
        this.props.verticalScrollbarShow &&
        <VerticalScrollBar
          zIndex={1}
          value={parseInt(this.props.verticalScrollbarProgress)}/>
      }

      <TextParts ref="text" zIndex={0} {...this.props} />
    </Border>);

  },

  componentDidMount : function(){
    $(ReactDOM.findDOMNode(this)).on(appTypes.CUSTOM_EVENT.ON_RESIZE,this.onResize)
  },

  onResize : function(e){
    $(ReactDOM.findDOMNode(this.refs.text)).trigger(appTypes.CUSTOM_EVENT.ON_PAINT);
  }

});

module.exports = TextArea;



